body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
#root,
.container {
  height: 100%;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


.Button {
  height: 60px;
  border-radius: 4px;
}

.Numpad {
  display: grid;
  grid-template-columns: 90px 90px 90px;
  grid-gap: 10px;
}

.Numpad .two-col {
  grid-column-end: span 2;
}

.FoodItems,
.ExtraButtons {
  display: grid;
  grid-template-columns: 90px 90px 90px 90px;
  grid-gap: 10px;
}

.ExtraButtons {
  padding-top: 10px;
}

.two-col {
  grid-column-end: span 2;
}

.screen {
  display: flex;
  flex-direction: column;
}

.buttons {
  display: flex;
  flex-direction: row;
}

.totals {
  display: flex;
  flex-direction: row;
  padding: 5px;
}

.buttons > div {
  padding: 5px;
}

.food-items {
  align-self: flex-end;
}

.orders {
  padding: 5px;
  display: grid;
  grid-template-columns: 340px 340px;
  grid-gap: 10px;
}

.orders .order-details {
  border: 1px solid #ccc;
  padding: 5px;
}

.totals {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.pay-now-button {
  height: 40px;
  width: 190px;
}

#total,
#change-due,
#cash {
  display: flex;
  align-items: center;
  border: 1px solid #ccc;
  height: 38px;
  width: 145px;
  padding: 0 5px 0 5px;
}

.order-details {
  height: 150px;
}

